@import url(https://fonts.googleapis.com/css?family=Open+Sans&display=swap);
*,*::before,*::after{box-sizing:border-box}html{font-size:100%}ul{list-style:none;margin:0;padding:0}a{color:#dadce1;text-decoration:none}body{padding:0;min-height:100vh;margin:0;font-family:-apple-system, BlinkMacSystemFont, 'Open Sans' 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}


.navigationBar{height:7vh;background-color:#242526;padding:0 1rem;border-bottom:1px solid #474a4d}.navigationBarContents{max-width:100%;height:100%;display:flex;justify-content:flex-end}

